export default {
	data() {
		return {
			shareInfo: {
			  headerImg: 'https://wx.qlogo.cn/mmopen/vi_32/K0OnM6OdM8IQmv9rRACKSz1WSBB93KKyBhRmGWQ7VWAgro99R7ianAOibNbuZ2rhib7RtvLIHIrU40JdFcGfWZR8Q/132',
			  bgImg: '../../static/poster.jpg',
			  qrcode: 'https://wx.qlogo.cn/mmopen/vi_32/K0OnM6OdM8IQmv9rRACKSz1WSBB93KKyBhRmGWQ7VWAgro99R7ianAOibNbuZ2rhib7RtvLIHIrU40JdFcGfWZR8Q/132'
			},
			width: 0,
			height: 0,
			systemInfo: {},
			poster: '' //生成的海报
		}
	},
	onLoad(options) {
		let that = this
		wx.getSystemInfo({
		  success (res) {
			  that.systemInfo = res;
			  console.log('wx.getSystemInfo',res)
		    // 通过像素比计算出画布的实际大小（330x490）是展示的出来的大小
		    that.width = 330 * res.pixelRatio
		    that.height = 490 * res.pixelRatio
		  }
		})
	},
	onShow() {
		let that = this;
		var query = wx.createSelectorQuery()
		query.select('#canvasPoster').boundingClientRect((res) => {
			// 返回值包括画布的实际宽高
			that.drawImage(res);
		}).exec();
	},
	methods: {
		saveImg(e) {
			let that = this;
		    wx.showLoading({
		      title: '保存中...'
		    })
		    wx.downloadFile({
		      url: that.poster,
		      success: function (res) {
		        //图片保存到本地
		        wx.saveImageToPhotosAlbum({
		          filePath: res.tempFilePath,
		          success: function (data) {
		            wx.hideLoading()
		            wx.showModal({
		              title: '提示',
		              content: '您的推广海报已存入手机相册，赶快分享给好友吧',
		              showCancel:false,
		            })
		          },
		          fail: function (err) {
		            if (err.errMsg === "saveImageToPhotosAlbum:fail:auth denied" || err.errMsg === "saveImageToPhotosAlbum:fail auth deny") {
		              // 这边微信做过调整，必须要在按钮中触发，因此需要在弹框回调中进行调用
		              wx.showModal({
		                title: '提示',
		                content: '需要您授权保存相册',
		                showCancel: false,
		                success:modalSuccess=>{
		                  wx.openSetting({
		                    success(settingdata) {
		                      console.log("settingdata", settingdata)
		                      if (settingdata.authSetting['scope.writePhotosAlbum']) {
		                        wx.showModal({
		                          title: '提示',
		                          content: '获取权限成功,再次点击图片即可保存',
		                          showCancel: false,
		                        })
		                      } else {
		                        wx.showModal({
		                          title: '提示',
		                          content: '获取权限失败，将无法保存到相册哦~',
		                          showCancel: false,
		                        })
		                      }
		                    },
		                    fail(failData) {
		                      console.log("failData",failData)
		                    },
		                    complete(finishData) {
		                      console.log("finishData", finishData)
		                    }
		                  })
		                }
		              })
		            }
		          },
		          complete(res) {
		            wx.hideLoading()
		          }
		        })
		      }
		    })
		},
		previewImg () {
		    if (this.poster) {
		      //预览图片，预览后可长按保存或者分享给朋友
		      wx.previewImage({
		        urls: [this.poster]
		      })
		    }
		 },
		drawImage (canvasAttrs) {
			let that = this;
		    let ctx = wx.createCanvasContext('canvasPoster', this)
			console.log('ctx',ctx)
		    let canvasW = canvasAttrs.width // 画布的真实宽度
		    let canvasH = canvasAttrs.height //画布的真实高度
		    // 头像和二维码大小都需要在规定大小的基础上放大像素比的比例后面都会 *this.systemInfo.pixelRatio
		    let headerW = 48 * this.systemInfo.pixelRatio
		    let headerX = (canvasW - headerW) / 2
		    let headerY = 40 * this.systemInfo.pixelRatio
		    let qrcodeW = 73 * this.systemInfo.pixelRatio
		    let qrcodeX = 216 * this.systemInfo.pixelRatio
		    let qrcodeY = 400 * this.systemInfo.pixelRatio
		    // 填充背景
		    ctx.drawImage(this.shareInfo.bgImg, 0, 0, canvasW, canvasH)
		    ctx.save()
			//测试
			ctx.drawImage('../../static/logo.png', 400, 600, 100, 100)
			ctx.save()
		    // 控制头像为圆形
		    // ctx.setStrokeStyle('rgba(0,0,0,.2)') //设置线条颜色，如果不设置默认是黑色，头像四周会出现黑边框
		    // ctx.arc(headerX + headerW / 2, headerY + headerW / 2, headerW / 2, 0, 2 * Math.PI)
		    // ctx.stroke()
		    // //画完之后执行clip()方法，否则不会出现圆形效果
		    // ctx.clip()
		    // // 将头像画到画布上
		    // ctx.drawImage(this.shareInfo.headerImg, headerX, headerY, headerW, headerW)
		    // ctx.restore()
		    // ctx.save()
		    // // 绘制二维码
		    // ctx.drawImage(this.shareInfo.qrcode, qrcodeX, qrcodeY, qrcodeW, qrcodeW)
		    // ctx.save()
		    ctx.draw()
		    setTimeout(() => {
		      //下面的13以及减26推测是因为在写样式的时候写了固定的zoom: 50%而没有用像素比缩放导致的黑边，所以在生成时进行了适当的缩小生成，这个大家可以自行尝试
		      wx.canvasToTempFilePath({
		        canvasId: 'canvasPoster',
		        x: 13,
		        y: 13,
		        width: canvasW - 26,
		        height: canvasH - 26,
		        destWidth: canvasW - 26,
		        destHeight: canvasH - 26,
		        success: (res) => {
		          that.poster = res.tempFilePath
				  console.log('canvasToTempFilePath',res)
		        }
		      })
		    }, 200)
		}
	}
}
